"use client"

import { useEffect, useState } from "react"
import { useForm } from "react-hook-form"
import { zodResolver } from "@hookform/resolvers/zod"
import * as z from "zod"
import { useRouter } from "next/navigation"
import { Loader2, Save, User, Lock } from "lucide-react"

import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form"
import { Input } from "@/components/ui/input"
import { Separator } from "@/components/ui/separator"
import { useToast } from "@/components/ui/use-toast"
import { usersApi } from "@/lib/api/users-api"
import { useAuth } from "@/contexts/auth-context"

// --- 1. ESQUEMA DE VALIDACIÓN (ZOD) ---
const profileFormSchema = z.object({
  fullName: z.string().min(2, "El nombre debe tener al menos 2 caracteres."),
  username: z.string().min(2, "El usuario debe tener al menos 2 caracteres."),
  email: z.string().email("Email inválido."),
  // La contraseña es opcional. Si está vacía, no se cambia.
  password: z.string().optional(),
  confirmPassword: z.string().optional(),
}).refine((data) => {
  // Validación: Si escribe password, debe escribir confirmación y coincidir
  if (data.password && data.password !== data.confirmPassword) {
    return false;
  }
  return true;
}, {
  message: "Las contraseñas no coinciden.",
  path: ["confirmPassword"],
});

type ProfileFormValues = z.infer<typeof profileFormSchema>

export default function ProfilePage() {
  const { setUser } = useAuth() // Para actualizar el nombre en la navbar si cambia
  const { toast } = useToast()
  const router = useRouter()
  const [isLoading, setIsLoading] = useState(false)

  // --- 2. CONFIGURACIÓN DEL FORMULARIO ---
  const form = useForm<ProfileFormValues>({
    resolver: zodResolver(profileFormSchema),
    defaultValues: {
      fullName: "",
      username: "",
      email: "",
      password: "",
      confirmPassword: "",
    },
    mode: "onChange",
  })

  // --- 3. CARGAR DATOS AL INICIO ---
  useEffect(() => {
    const loadProfile = async () => {
      try {
        // Usamos el endpoint /me que ya configuramos en usersApi
        const userData = await usersApi.getMe()
        
        // Rellenamos el formulario con los datos actuales
        form.reset({
          fullName: userData.fullName,
          username: userData.username,
          email: userData.email,
          password: "",       
          confirmPassword: "" 
        })
      } catch (error) {
        console.error(error)
        toast({
          variant: "destructive",
          title: "Error",
          description: "No se pudo cargar la información del perfil.",
        })
      }
    }
    loadProfile()
  }, [form, toast])

  // --- 4. GUARDAR CAMBIOS ---
  async function onSubmit(data: ProfileFormValues) {
    setIsLoading(true)
    try {
      // Preparamos el objeto para enviar
      const payload: any = {
        fullName: data.fullName,
        username: data.username,
        email: data.email,
      }
      
      // Solo enviamos la contraseña si el usuario escribió algo
      if (data.password && data.password.trim() !== "") {
        payload.password = data.password
      }

      // Llamada a la API (PUT /users/me)
      const updatedUser = await usersApi.updateProfile(payload)

      // Actualizamos el contexto global (Navbar, etc.)
      if (setUser) setUser(updatedUser)

      toast({
        title: "Perfil actualizado",
        description: "Tus datos se han guardado correctamente.",
      })
      
      // Limpiamos los campos de contraseña por seguridad
      form.setValue("password", "")
      form.setValue("confirmPassword", "")
      router.refresh()
      
    } catch (error: any) {
      toast({
        variant: "destructive",
        title: "Error al actualizar",
        description: error.response?.data?.message || "Hubo un problema al guardar los cambios.",
      })
    } finally {
      setIsLoading(false)
    }
  }

  return (
    <div className="container max-w-3xl py-10 space-y-8 animate-in fade-in-50">
      <div>
        <h3 className="text-3xl font-bold tracking-tight">Mi Perfil</h3>
        <p className="text-muted-foreground">
          Gestiona tu información personal y credenciales de acceso.
        </p>
      </div>
      
      <Separator />

      <Form {...form}>
        <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8">
          
          {/* SECCIÓN 1: DATOS PERSONALES */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <User className="w-5 h-5" /> Información Básica
              </CardTitle>
              <CardDescription>
                Estos datos son visibles en el sistema.
              </CardDescription>
            </CardHeader>
            <CardContent className="grid gap-6">
              <FormField
                control={form.control}
                name="fullName"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>Nombre Completo</FormLabel>
                    <FormControl>
                      <Input placeholder="Tu nombre completo" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
              
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <FormField
                  control={form.control}
                  name="username"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Usuario</FormLabel>
                      <FormControl>
                        <Input placeholder="username" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="email"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Correo Electrónico</FormLabel>
                      <FormControl>
                        <Input placeholder="email@ejemplo.com" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>
            </CardContent>
          </Card>

          {/* SECCIÓN 2: SEGURIDAD (CONTRASEÑA) */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <Lock className="w-5 h-5" /> Seguridad
              </CardTitle>
              <CardDescription>
                Deja estos campos en blanco si <strong>no</strong> quieres cambiar tu contraseña actual.
              </CardDescription>
            </CardHeader>
            <CardContent className="grid gap-6">
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <FormField
                  control={form.control}
                  name="password"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Nueva Contraseña</FormLabel>
                      <FormControl>
                        <Input type="password" placeholder="********" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="confirmPassword"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Confirmar Contraseña</FormLabel>
                      <FormControl>
                        <Input type="password" placeholder="********" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </div>
            </CardContent>
          </Card>

          <div className="flex justify-end gap-4">
            <Button type="button" variant="outline" onClick={() => router.back()}>
              Cancelar
            </Button>
            <Button type="submit" disabled={isLoading} className="min-w-35">
              {isLoading ? (
                <Loader2 className="mr-2 h-4 w-4 animate-spin" />
              ) : (
                <Save className="mr-2 h-4 w-4" />
              )}
              Guardar Cambios
            </Button>
          </div>
        </form>
      </Form>
    </div>
  )
}